const articles = __RES__.contentsSortedByTime.slice(0, 6).map(art => {
  art = __RES__.contents[art]

  return {
    title: art.title,
    thumb: art.desc.slice(0, 200),
    url: `https://blog.ijason.cc/article/${art.name}`,
    time: art.mtime
  }
})

