<template>
  <div class="page projects">
    <h1 class="title">工作经验</h1>
    <div class="pagebody">
      <div class="cardContainer" @touchmove="touchmove" @touchend="touchend">
        <div
          class="card"
          v-for="(project, idx) in $store.state.data.website.projects"
          :class="{
            currentSection: idx === currentPage,
            preSection: idx < currentPage,
            nextSection: idx > currentPage
          }"
          :style="{
            'z-index': idx < currentPage ? idx : $store.state.data.website.projects.length - idx
          }"
          >
          <h4>{{ project.name }}</h4>
          <p style="margin-bottom: 20px; font-size: 14px;">{{ project.time }}</p>
          <p class="description">{{ project.desc }}</p>
          <p class="description">{{ project.desc_2 }}</p>
          <p class="description">{{ project.desc_3 }}</p>
          <p class="description">{{ project.desc_4 }}</p>
          <p class="description">{{ project.desc_5 }}</p>
          <p class="description">{{ project.desc_6 }}</p>
          <p class="description">{{ project.desc_7 }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'projects',
  props: ['id'],
  data: () => ({
    activeQrcodeUrl: '',
    showQrcode: false,
    touchStartY: 0,
    currentPage: 0
  }),
  methods: {
    showQrcodeFunc (url) {
      this.showQrcode = true
      this.activeQrcodeUrl = url
    },
    hideQrcode () {
      this.showQrcode = false
    },
    touchmove (e) {
      if (this.touchStartX !== 0) return
      this.touchStartX = e.changedTouches[0].screenX
    },
    touchend (e) {
      if (this.touchStartX === 0) return

      const touchEndX = e.changedTouches[0].screenX

      if (this.touchStartX - touchEndX > 20) {
        if (this.currentPage === this.$store.state.data.website.projects.length - 1) return
        else this.currentPage++
      } else if (this.touchStartX - touchEndX < -20) {
        if (this.currentPage === 0) return
        else this.currentPage--
      }

      this.touchStartX = 0
    }
  }
}
</script>

<style lang="less" scoped>
@import 'index.less';
</style>



